* {
  box-sizing: border-box;
}

html,
body {
 background-color: #000a33; 
  height: 100vh;
}

.App {
  font-family: sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.Screen {
  position: relative;
}

.Button {
  position: absolute;
}

.Button.debug {
  font-size: 10px;
  background-color: red;
  opacity: 0.9;
  color: white;
  text-decoration: none;
  padding: 2px;
  overflow: hidden;
}

.wrapper {
  padding: 20px;
}
